<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'TruyenItem',
  props: ['truyen'],
  methods:{
    getNames(t){
return t.replace(/^(.{20}[^\s]*).*/, "$1");
    },
    getName(s){
      let a= 20;
      if(s.length>a){
        return s.substring(0, a)+"...";
      }else{
        return s;
      }
    }
  }
}
