
export const DataTruyen = {
    getTruyenTrangChu() {
        var arr = [];
        while (arr.length < 10) {
            var t = Math.floor(Math.random() * 7);
            var arrt = [];
            if (t === 0) {
                arrt = this.coTichVietNam();
            } else if (t === 1) {
                arrt = this.coTichTheGioi();
            } else if (t === 2) {
                arrt = this.truyenDanGian();
            } else if (t === 3) {
                arrt = this.truyenNguNgon();
            } else if (t === 4) {
                arrt = this.truyenCuoi();
            } else if (t === 5) {
                arrt = this.truyenQuaTangCuocSong();
            } else if (t === 6) {
                arrt = this.truyenThuVien();
            }
            if (arrt.length > 0) {
                var ts = Math.floor(Math.random() * arrt.length);
                var add = true;
                for (let i = 0; i < arr.length; i++) {
                    var tt = arr[i];
                    if (tt.ten === arrt[ts].ten) {
                        add = false;
                        break;
                    }
                }
                if (add) {
                    arr.push(arrt[ts]);
                }
            }
        }
        return arr;
    },
    coTichVietNam() {
        return cotichvn;
    },
    coTichTheGioi() {
        return cotichtg;
    },
    truyenDanGian() {
        return truyendangian;
    },
    truyenNguNgon() {
        return truyenngungon;
    },
    truyenCuoi() {
        return truyencuoi;
    },
    truyenQuaTangCuocSong() {
        return truyenquatangcuocsong;
    },
    truyenThuVien() {
        return truyenthuvien;
    }
}
